import './Home.css';
import { useState, useEffect } from 'react';
import back1 from './backspace1.png';

function EvalFunction() {
    const [inputValue, setInputValue] = useState('');
    const [result, setResult] = useState('');

    useEffect(() => {
        document.getElementById('result').innerHTML = result;
    }, [result]);

    function equals() {
        try {
            const output = eval(inputValue);
            setResult(output.toString());
        } catch (error) {
            setResult('Error');
        }
    }

    function OnClickFunction(value) {
        setInputValue(inputValue + value);
    }

    function OnKeyDownFunction(event) {
        const key = event.key;
        if (key === '+' || key === '-' || key === '/' || key === '*' || key === '%') {
            setInputValue(inputValue + key);
        } else if (key >= '0' && key <= '9') {
            setInputValue(inputValue + key);
        } else if (key === 'Enter' || key === '=') {
            equals();
        } else if (key === 'Escape' || key === 'C' || key === 'c') {
            clear();
        } else if (key === 'Backspace') {
            back();
        }
    }

    function clear() {
        setInputValue('');
        setResult('');
    }

    function back() {
        let str = inputValue;
        str = str.substring(0, str.length - 1);
        setInputValue(str);
    }

    return (
        <div>
            <div className='wrapper'>
                <div className='container'>
                    <div className='row1coltype'>
                        <div className='col1'>
                            <label>Enter Expression: </label>
                            <input type='text' className='inputText' id='text' value={inputValue} onKeyDown={OnKeyDownFunction} />

                        </div>
                        <div className='col1'>
                            <label>Output</label>
                            <label id='result' placeholder=''></label>
                        </div>
                    </div>
                    <div className='row2'>
                        <button className='b1' onClick={clear}>C</button>
                        <button className='b1' onClick={() => OnClickFunction('%')}>% </button>
                        <button className='b1' onClick={() => OnClickFunction('/')}>/</button>
                        <img src={back1} onClick={back} className='b1 bimg' />
                    </div>
                    <div className='row3'>
                        <button className='b2' onClick={() => OnClickFunction('7')}>7</button>
                        <button className='b2' onClick={() => OnClickFunction('8')}>8</button>
                        <button className='b2' onClick={() => OnClickFunction('9')}>9</button>
                        <button className='b1' onClick={() => OnClickFunction('*')}>*</button>
                    </div>
                    <div className='row4'>
                        <button className='b2' onClick={() => OnClickFunction('4')}>4</button>
                        <button className='b2' onClick={() => OnClickFunction('5')}>5</button>
                        <button className='b2' onClick={() => OnClickFunction('6')}>6</button>
                        <button className='b1' onClick={() => OnClickFunction('-')}>-</button>
                    </div>
                    <div className='row5'>
                        <button className='b2' onClick={() => OnClickFunction('1')}>1</button>
                        <button className='b2' onClick={() => OnClickFunction('2')}>2</button>
                        <button className='b2' onClick={() => OnClickFunction('3')}>3</button>
                        <button className='b1' onClick={() => OnClickFunction('+')}>+</button>
                    </div>
                    <div className='row6'>
                        <button className='b2' onClick={() => OnClickFunction('00')}>00</button>
                        <button className='b2' onClick={() => OnClickFunction('0')}>0</button>
                        <button className='b2' onClick={() => OnClickFunction('.')}>.</button>
                        <button className='b3' onClick={equals}>=</button>
                    </div>

                </div>
            </div>
        </div>
    );
}

export default EvalFunction;
